import React from "react";

function ImageList(props) {
  return (
    <div className="imagelist">
      {props.characters.map(charecter => (
        <img
          src={charecter.image}
          key={charecter}
          alt="90s cartoon charecters"
          onClick={() => props.onImageClick(charecter.id)}
        />
      ))}
    </div>
  );
}

export default ImageList;
