import React from "react";
import Footer from "./Footer";
import Header from "./Header";
import ToolTip from "./ToolTip";
import ImageList from "./ImageList";

class ClickyGamePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      score: 0,
      characters: [
        {
          id: 1,
          name: "SpongeBob",
          image:
            "https://vignette.wikia.nocookie.net/spongebobgalaxy/images/0/07/SpongeBob_SquarePants.png/revision/latest?cb=20171228024014",
          occupation: "Fry Cook",
          location: "A Pineapple Under the Sea",
          clickedOn: false
        },
        {
          id: 2,
          name: "Mr. Krabs",
          image:
            "https://vignette3.wikia.nocookie.net/vsbattles/images/8/80/Mr._Krabs.png/revision/latest?cb=20150919162131",
          occupation: "Restaurant Owner",
          location: "A Giant Anchor",
          clickedOn: false
        },
        {
          id: 3,
          name: "Squidward",
          image:
            "https://vignette2.wikia.nocookie.net/fictionalcharacters/images/a/ac/Squidward.png/revision/latest?cb=20131121012626",
          occupation: "Cashier",
          location: "An Easter Island Head",
          clickedOn: false
        },
        {
          id: 4,
          name: "Dexter",
          image:
            "https://s-media-cache-ak0.pinimg.com/originals/fe/32/49/fe32495d45283cd6860ae122f0aeaad9.png",
          occupation: "Boy Genius",
          location: "A Secret Laboratory",
          clickedOn: false
        },
        {
          id: 5,
          name: "Courage",
          image:
            "https://vignette4.wikia.nocookie.net/vsbattles/images/3/39/Courage-0.png/revision/latest?cb=20160719055423",
          occupation: "A Cowardly Dog",
          location: "Nowhere, Kansas",
          clickedOn: false
        },
        {
          id: 6,
          name: "Doug Funnie",
          image:
            "https://vignette1.wikia.nocookie.net/doug/images/3/3b/Doug001.gif/revision/latest?cb=20110807170511",
          occupation: "Student",
          location: "Bluffington",
          clickedOn: false
        },
        {
          id: 7,
          name: "Bugs Bunny",
          image:
            "https://vignette2.wikia.nocookie.net/deathbattlefanon/images/2/2b/Bugs_Bunny.png/revision/latest?cb=20151206010607",
          occupation: "Looney Toon",
          location: "A Rabbit Burrow",
          clickedOn: false
        },
        {
          id: 8,
          name: "Johnny Bravo",
          image:
            "http://vignette3.wikia.nocookie.net/p__/images/9/9a/Johnny_Bravo.png/revision/latest?cb=20131031233339&path-prefix=protagonist",
          occupation: "Ladies Man",
          location: "Aron City",
          clickedOn: false
        },
        {
          id: 9,
          name: "Tommy Pickles",
          image:
            "https://vignette4.wikia.nocookie.net/uncyclopedia/images/e/e4/Tommy_Pickles.png/revision/latest?cb=20110530102641",
          occupation: "Adventurer",
          location: "California",
          clickedOn: false
        },
        {
          id: 10,
          name: "Rocko",
          image:
            "http://vignette3.wikia.nocookie.net/rockosmodernlife/images/9/9e/Rockos-modern-life-4e582a8f06a41.png/revision/latest?cb=20130522173416",
          occupation: "Cashier",
          location: "O-Town, California",
          clickedOn: false
        },
        {
          id: 11,
          name: "Captain Planet",
          image:
            "http://vignette2.wikia.nocookie.net/dragon-rap-battles/images/b/b6/Captain_Planet.png/revision/latest?cb=20160911200836",
          occupation: "Superhero",
          location: "Earth",
          clickedOn: false
        },
        {
          id: 12,
          name: "Ickis",
          image: "http://static.tvtropes.org/pmwiki/pub/images/ickis.png",
          occupation: "Ahhhh! A Real Monster!",
          location: "Under a City Dump",
          clickedOn: false
        }
      ]
    };
  }
  onImageClick = characterId => {
    let userScore = this.state.score + 1;
    this.setState({ score: userScore });

    if (this.state.characters.find(x => x.id === characterId).clickedOn) {
      this.setState({ score: 0 });
    }

    let myCharacters = this.state.characters;
    let index = myCharacters.findIndex(x => x.id === characterId);
    myCharacters[index].clickedOn = true;
    this.setState({
      characters: myCharacters
    });
    this.setState({ characters: this.shuffle(this.state.characters) });
  };
  shuffle(a) {
    var j, x, i;
    for (i = a.length - 1; i > 0; i--) {
      j = Math.floor(Math.random() * (i + 1));
      x = a[i];
      a[i] = a[j];
      a[j] = x;
    }
    return a;
  }
  render() {
    return (
      <div className="App">
        <Header />
        <ToolTip text={"90's cartoons are awesome!"}>
          <ImageList
            className="images"
            characters={this.state.characters}
            onImageClick={this.onImageClick}
          />
        </ToolTip>

        <div className="score-container">
          <h1 className="score">Score:{this.state.score}</h1>
        </div>
        <Footer />
      </div>
    );
  }
}

export default ClickyGamePage;
